.container {
  width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 175px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 5;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.modal-show {
  background-color: rgba(56, 56, 56, .5);
}

.modal {
  pointer-events: auto;
  background-color: #e9f5f6;
  border-radius: 30px;
  padding: 20px;
  display: none;
  position: absolute;
}

.modal.is-show {
  display: block;
}

.close-modal-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.student-list {
  list-style: none;
  display: block;
}

.student-list li {
  width: 500px;
  background-color: #e9f5f6;
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  padding: 25px;
}

.delete-button {
  color: #fff;
  background-color: #f10530;
  border: none;
  border-radius: 10px;
  padding: 12px;
  font-family: Montserrat, sans-serif;
}

.update-button {
  color: #fff;
  background-color: #47b404;
  border: none;
  border-radius: 10px;
  padding: 12px;
  font-family: Montserrat, sans-serif;
}

.get-students-btn, .update-form-btn {
  color: #fff;
  background-color: #2d7ee9;
  border: none;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: block;
}

.update-form-btn {
  margin-top: 20px;
}

.add-form-title {
  text-align: center;
}

.add-form, .update-form {
  width: 800px;
  height: 550px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.add-form {
  background-color: #e9f5f6;
  border-radius: 50px;
}

.add-form:not(:first-child), .add-form label:not(:first-child) {
  margin-top: 20px;
}

.add-form input:not(:last-child) {
  margin-top: 5px;
}

.add-form input:last-child {
  margin-left: 5px;
}

.form-submit {
  color: #fff;
  background-color: #2d7ee9;
  border: none;
  border-radius: 20px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: block;
}

